import { Injectable } from '@angular/core';
import { STORAGE_KEYS } from '../config/storage_keys.config';
import { LocalUser } from '../models/local_user';
import { UserService } from './user/user.service';

@Injectable({
  providedIn: 'root'
})
export class StorageService {
  private id: string;

  private selectedMesa = {
    idMesa: "",
    idJogador: "",
    tipo: ""
  };

  constructor() { }

  getSelectedMesa() {
    this.selectedMesa = JSON.parse(localStorage.getItem('mesa'));
    return this.selectedMesa;
  }

  setSelectedMesa(idMesa, idJogador, tipo) {
    this.selectedMesa = {
      idJogador,
      idMesa,
      tipo
    }

    localStorage.setItem('mesa', JSON.stringify(this.selectedMesa));
  }

  setNull(){
    localStorage.removeItem('mesa');
    this.selectedMesa = null;
  }

  getLocalUser(): LocalUser {
    let user = localStorage.getItem(STORAGE_KEYS.localUser);
    if (user == null) {
      return null;
    }
    else {
      return JSON.parse(user);
    }
  }

  setLocalUser(obj: LocalUser) {
    if (obj == null) {
      localStorage.removeItem(STORAGE_KEYS.localUser);
    }
    else {
      localStorage.setItem(STORAGE_KEYS.localUser, JSON.stringify(obj));
    }

  }

  getLocalUserId() {
    return this.getLocalUser().id;
  }

  setLocalUserId(id: string) {
    let obj = this.getLocalUser();
    obj.id = id;
    this.setLocalUser(obj);
  }
}
