import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpHandler, HttpRequest, HttpEvent, HTTP_INTERCEPTORS } from '@angular/common/http';
import { Observable, throwError } from "rxjs";
import { catchError } from 'rxjs/operators';
import { StorageService } from '../services/storage-service.service';
import { AlertController } from '@ionic/angular';
import { FieldMessage } from '../models/field_message';


@Injectable()
export class ErrorInterceptor implements HttpInterceptor {

    constructor(public storage: StorageService, public alertContr: AlertController) { }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(req)
            .pipe(
                catchError(error => {
                    //console.log(error)
                    if (!error.status) {
                        try {
                            error = JSON.parse(error);
                        } catch (e) {

                        }

                    }
                    switch (error.status) {
                        case 403: this.handle403();
                            break;

                        case 401: this.handle401(error.error);
                            break;

                        case 422:
                            this.handle422(error.error);
                            break;

                        case 404:
                            this.handle404(error.error);
                            break;

                        default:
                            this.handleDefaultError(error.error);
                    }

                    return throwError(error);
                })) as any;
    }

    async createAlert(error) {
        let titulo = error.error;

        if (!titulo)
            titulo = "Falha"


        let alert = await this.alertContr.create({
            header: error.status + ": " + titulo,
            message: error.msg,
            backdropDismiss: false,
            buttons: [
                {
                    text: 'OK'
                }
            ]
        });

        await alert.present();
    }

    handle401(e) {
        let error = JSON.parse(e);
        this.createAlert(error);
    }

    async handle404(e) {
        let alert = await this.alertContr.create({
            header: e.status + ": Não encontrado",
            message: e.msg,
            backdropDismiss: false,
            buttons: [
                {
                    text: 'OK'
                }
            ]
        });

        await alert.present();

    }

    handle403() {
        this.storage.setLocalUser(null);
    }

    async handle422(e) {
        let alert = await this.alertContr.create({
            header: 'Erro 422: Validação',
            message: this.listErrors(e.errors),
            backdropDismiss: false,
            buttons: [
                {
                    text: 'Ok'
                }
            ]
        });
        alert.present();

    }

    handleDefaultError(e) {
        this.createAlert(e);
    }

    listErrors(messages: FieldMessage[]) {
        let s: string = '';
        for (var i = 0; i < messages.length; i++) {
            s = s + '<p><strong>' + messages[i].fieldName + "</strong>: " + messages[i].message + '</p>';
        }
        return s;
    }


}


export const ErrorInterceptorProvider = {
    provide: HTTP_INTERCEPTORS,
    useClass: ErrorInterceptor,
    multi: true,
};